*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}


.btn-signout{
    display: flex;
    border: solid #b6afb2 2px;
    border-radius: 5px;
    margin-left: auto;
    font-size: 20px;
    color:rgb(255, 255, 255);
    font-weight: 200;
    text-align: center;
    font-family:sans-serif;
    background: #b9acb1;
    padding: 5px 10px;
    margin-bottom: auto;
    text-decoration: none;
    text-transform: capitalize;
    letter-spacing: 2px;
    transition: 0.5s;
}

button:hover{
    letter-spacing: 4px;
}
