.Gallery-container{
    
    display:flex;
    flex-wrap: wrap;
    margin-right: 5px;
    margin-bottom: 5px;
    width: 100%;
    padding: 20px;
    position: relative;
    top:120px;
}

.ol-List{    
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    list-style-type: none;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    
    min-width: 200px;
    margin: 20px;
}

.ol-List li{
   align-items: center;
   border-radius: 3px;
   color: rgb(8, 8, 8);
   font-weight: 100;
   font-size: large;
}

.card-style{
    background-color: rgb(255, 255, 255);
    margin: 10px 10px;
}

.card-style:hover{
    background-color: rgb(219, 218, 205);
    
}
.ol-List li:hover{
    color:red;
    
    font-weight: 200;
}