*{
    margin:0;
    padding:0;
}

.paperStyle{
     
 padding:50px 40px;
 position: absolute;
 width: auto;
 margin: 20px 20px;
 max-width: 300px;
}

.main-container{
    
}
.grid-container{
    position: relative;
    display: flex;
    align-items: center;
    justify-content:space-around;
    padding:70px;
    background:linear-gradient(to bottom, rgb(255, 204, 1), rgba(46, 45, 43, 0.918));
    margin: auto auto;
    height: 100vh;
    max-width: auto;
    background-size: cover;
    
}

  @media only screen and (min-width:768px) {
    main-container{
      width:auto;  
       
    } 
  }
  @media only screen and (min-width:992px) {
    main-container{
    width:auto;    
    }
}
@media only screen and (min-width:500px) {
    main-container{
    width:auto;   
    margin: auto;
    }
}
@media only screen and (min-width:300px) {
    main-container{
    width:50%;   
    margin: auto;
    }
}

